h1 {
  text-align: center;
  font-size: 3em;
  color: rgb(32, 59, 93);
}

h2 {
  font-size: 1.8em;
  color: rgb(32, 59, 93);
}

h3 {
  font-size: 1.2em;
  line-height: 0em;
  color: rgb(32, 59, 93);
}

li {
  font-size: 1.2em;
  margin-bottom: 0.75em;
  font-weight: 550;
  color: rgb(32, 59, 93);
}

button:hover {
  cursor: pointer;
}

.favorites {
  background-color: rgb(228,250,255,255);
  width: 15em;
  position: fixed;
  top: 4em;
  right: 0;
  padding-bottom: 2em;
  padding-left: 2.5em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
}

.favorites input {
  margin-bottom: 0.9em;
}

.favorites button {
  font-size: 1.2em;
  font-weight: bold;
  color: rgb(32, 59, 93);
  background-color: rgb(198,214,43,255);
  border-color: black;
  border-width: 0.15em;
  border-radius: 1em;
  margin-top: 1em;
  padding: 0.5em 1em;
}

.product {
  display: flex;
  flex-wrap: wrap;
}

.item {
  background-color: rgb(44,176,211,255);
  width: 35em;
  margin: 1.5em;
  padding: 2em;
  border-radius: 1.5em;
}

.item img {
  float: left;
  max-width: 20em;
  max-height: 20em;
  object-fit: cover;
  margin-right: 4em;
  margin-bottom: 4em;
}

.item button {
  font-size: 1.2em;
  font-weight: bold;
  color: rgb(32, 59, 93);
  background-color: rgb(198,214,43,255);
  border-color: rgb(0, 0, 0);
  border-width: 0.18em;
  border-radius: 1em;
  padding: 0.5em 1em;
  margin-top: 1em;
  margin-right: 1em;
  float: right;
}

.toggle-button {
  position: fixed;
  top: 1em;
  right: 2em;
  font-size: 1.2em;
  font-weight: bold;
  color: rgb(32, 59, 93);
  background-color: rgb(198,214,43,255);
  border-color: rgb(0, 0, 0);
  border-width: 0.15em;
  border-radius: 1em;
  padding: 0.5em 1em;
  float: right;
}

label {
  line-height: 0em;
}

/* Responsive styles */
@media (max-width: 768px) {
  .favorites {
    width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    padding-left: 1em;
    padding-right: 1em;
    z-index: 1000;
    text-align: center; /* Center the text */
  }

  .toggle-button {
    position: fixed;
    top: 2em;
    right: 2em;
    font-size: 0.8em;
    border-width: 0.12em;
    border-radius: 0.67em;
    padding: 0.33em 0.67em;
    z-index: 1001;
  }

  .product {
    margin-top: 5em; /* Adjust this value if needed to avoid overlap with the navigation bar */
  }

  .item {
    width: 100%;
    margin: 1em 0;
  }

  .item img {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 1em;
  }
}

/* Navigation bar styles */
nav {
  background-color: rgb(44,176,211,255);
  padding-top: 1.8em;
  padding-bottom: 1.8em;
  padding-left: 5.2em;
  padding-right: 5.2em;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  align: right;
}

nav ul li {
  float: left;
}

nav ul li a {
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

nav ul li a:hover {
  #background-color: #111;
}
